.star-container {
    display: flex;
    justify-content: center;
    align-items: center;
}

.star-rating {
    margin-top: 30px;
    display: flex;
    flex-direction: column;
    justify-content: center;
    align-items: center;
}

h2 {
    text-align: center;
}
  
.app-container {
    display: flex;
    flex-direction: column;
    justify-content: center;
    align-items: center;
    height: 100vh;
    color: white;
}

.app-title {
    color: white;
}


input[type='radio'] {
    display: none;
}
  
.rating-rmk {
    height: 40px;
    margin-top: 20px;
}

  .open {
    position: fixed;
    bottom: 10px;
    right: 20px;
    background-color: blue;
    color: #fff;
    height: 40px;
    width: 140px;
    border-radius: 15px;
  }

  .close {
    color: red;
    font-size: 25px;
    font-weight: 900;
    cursor: pointer;
  }

  .modal-content {
    padding: 20px;
    position: absolute;
    background-color: #fff;
    color: #000;
    height: 200px;
    width: 250px;
  }

  .modal {
    position: absolute;
    top: 0;
    left: 0;
    height: 100dvh;
    width: 100vw;
    background-color: rgb(0 0 0 / 0.6);
    display: grid;
    place-items: center;
  }

  .dialog-msg {
    padding-top: 30px;
    font-weight: 700;
  }